.memenu {
    margin: 5px 0 0 0;
    padding: 0;
    width: 100%;
    list-style: none;
    display: inline-block;
    position: relative;
    font-size: 15px;
}
.memenu li{
	display:inline-block;
}
.memenu>li>a {
    float: left;
    padding-right: 45px;
    color: #000;
    font-size: 16px;
    text-decoration: none;
    transition: color .4s ease-in-out;
    -moz-transition: color .4s ease-in-out;
    -webkit-transition: color .4s ease-in-out;
    -o-transition: color .4s ease-in-out;
}
.memenu>li>.mepanel {
	position: absolute;
	display: none;
	background: #fff;
	width: 80%;
	top: 47px;
	left:0%;
	z-index: 99;
	padding: 20px 30px 20px;
	border: solid 1px #ccc;
	-webkit-box-sizing: border-box;
	-moz-box-sizing: border-box;
	box-sizing: border-box;
}
.me-one ul{
	padding:0;
}
.me-one ul li {
	list-style: none;
	margin: 0 0 7px 0;
	text-align: left !important;
	display: block;
}
.me-one ul li a {
    color: #999;
    font-size: 13px;
}
.me-one ul li a:hover{
	text-decoration:none;
	color:#73B6E1;
}
.me-one h4 {
    color: #73B6E1;
    font-size: 1.1em;
    text-align: left;
}
.memenu .mepanel ul{
	margin:0;
	padding:0;
}
.memenu .mepanel img{
	width:100%;border:solid 1px #dedede;
	cursor:pointer;-webkit-transition:border .3s linear;
	-moz-transition:border .3s linear;-o-transition:border .3s linear;transition:border .3s linear}.memenu .mepanel img:hover{border:solid 1px #777}.memenu form.contact input,.memenu form.contact textarea{font-family:Calibri,Arial;font-size:16px;color:#444;outline:0}
.mepanel .row{
	width:100%;
	margin-top:15px;
}
.mepanel .row:first-child{
	margin-top:0;
}
.mepanel .row:before,.mepanel .row:after{
	display:table;
	content:"";
	line-height:0;
}
.mepanel .row:after{
	clear:both
}
.mepanel .row .col1,.mepanel .row .col2,.mepanel .row .col3,.mepanel .row .col4,.mepanel .row .col5,.mepanel .row .col6{display:block;width:100%;min-height:20px;float:left;margin-left:2.127659574468085%;-webkit-box-sizing:border-box;-moz-box-sizing:border-box;box-sizing:border-box}
.mepanel .row [class*="col"]:first-child{margin-left:0}
.mepanel .row .col1 {
	width: 31.91%;
}
.mepanel .row .col2{
	width:31.914893617021278%;
}
.mepanel .row .col3{
	width:48.93617021276595%;
}
.mepanel .row .col4{
	width:65.95744680851064%;
}
.mepanel .row .col5{
	width:82.97872340425532%;
}
.mepanel .row .col6{
	width:100%
}
.memenu>li.showhide{
	display:none;
	width:100%;
	height:50px;
	cursor:pointer;
	color:#555;
	border-bottom:solid 1px rgba(0,0,0,0.1);
	background:#eee;
	background:url(data:image/svg+xml;base64,);background:-moz-linear-gradient(top,#eee 0,#dbdbdb 100%);background:-webkit-gradient(linear,left top,left bottom,color-stop(0%,#eee),color-stop(100%,#dbdbdb));background:-webkit-linear-gradient(top,#eee 0,#dbdbdb 100%);
	background:-o-linear-gradient(top,#eee 0,#dbdbdb 100%);
	background:-ms-linear-gradient(top,#eee 0,#dbdbdb 100%);
	background:linear-gradient(to bottom,#eee 0,#dbdbdb 100%);
	filter:progid:DXImageTransform.Microsoft.gradient(startColorstr='#eeeeee',endColorstr='#dbdbdb',GradientType=0)
}
.memenu>li.showhide span.title{
	margin:15px 0 0 25px;
	float:left;
}.memenu>li.showhide span.icon1:after{
	position:absolute;
	content:"";
	right:25px;
	top:15px;
	height:3px;
	width:25px;
	font-size:50px;
	border-top:3px solid #fff;
	border-bottom:3px solid #fff;
	z-index:1;
}
.memenu>li.showhide span.icon2:after{
	position:absolute;
	content:"";
	right:25px;
	top:27px;
	height:3px;
	width:25px;
	font-size:50px;
	border-top:3px solid #fff;
	border-bottom:3px solid #fff;
	z-index:1;
}	
.skyblue li>a,.skyblue>li.showhide span{
	color:#000;
}
.skyblue>li:hover>a,.skyblue>li.active>a,.skyblue .dropdown li:hover>a{
	color:#73B6E1;
}
.skyblue .mepanel img:hover,.skyblue form.contact input[type="text"]:focus,.skyblue form.contact textarea:focus{
	border:solid 1px #0194be;
}
.skyblue form.contact input[type="submit"]{
	background:#0194be;
}
@media(max-width:768px){
	.memenu>li{display:block;width:100%;box-sizing:border-box;-moz-box-sizing:border-box;-webkit-box-sizing:border-box}
	.memenu>li:hover,.memenu>li.active{border-top:0}
	.memenu>li>a{padding:15px 25px}
	.memenu>li:hover>a,.memenu>li.active>a{padding:15px 25px}
	.memenu a{width:100%;box-sizing:border-box;-moz-box-sizing:border-box;-webkit-box-sizing:border-box}
	.memenu .dropdown,.memenu .dropdown li .dropdown{width:100%;display:none;left:0;border-left:0;position:static;border:0;box-sizing:border-box;-moz-box-sizing:border-box;-webkit-box-sizing:border-box}
	.memenu .dropdown li{background:#fff!important;border:0}
	.memenu .dropdown>li>a{padding-left:40px!important}
	.memenu>li>.mepanel {
		position: static;
		margin-top: 50px;
		border: none;
		width: 100%;
		}
	.me-one ul li {
		text-align: left !important;
	}
	.memenu>li.showhide {
		height: 45px;
	}
	.mepanel .row{
		margin:0;
	}
	.mepanel .row [class*="col"]{float:none;display:block;width:100%;margin-left:0;margin-top:15px;-webkit-box-sizing:border-box;-moz-box-sizing:border-box;box-sizing:border-box}
	.mepanel .row:first-child [class*="col"]:first-child{margin-top:0}
	.mepanel .row{margin-top:0}.black{background:#222}
	}